.product{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;

}
.product__info{
    height: 10px;
}
.product__price{
    margin-top: 5px;
}

.product__rating{
    display: flex;
}
.product > img{
    width: 100%;
    max-height: 200px;
    object-fit: contain;
    margin-bottom: 15px;
}
.product__rating{
    display: inline-flex;

}
.product > button{
    background-color: orange;
    border-color: peru;
    border: 1px solid;
    margin-top: 5px;
    padding: 5px;
}